@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Merienda:wght@400;700&display=swap);
/*
* font-family: 'Merienda', cursive;
*/

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
  	margin: 0;
	font-family: 'Roboto', sans-serif;
}

code {
  	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.form {
    padding: 20px;
    align-items: center;
    height: 200px;
    margin-bottom: 25px;
}

.form h1 {
    display: block;
    margin-bottom: 19px;
    color: #ff7300;
}

.form input {
    display: block;
    margin-bottom: 13px;
    width: 230px;
    height: 35px;
    padding: 14px;
    color: #545454;
    font-size: 17px;
}

.form button {
    width: 80px;
    height: 30px;
}

p {
    margin-left: 20px;
    margin-top: 12px;
    font-size: 16px;
}

p span.span-class{
    color: #fe5800;
    font-size: 19px;
}
.main-container {
	margin-top: 13px;
	width: 100%;
	overflow: auto;
	padding: 13px;
	display: block;
}

.error-message {
	color: #ff0000;
	font-size: 17px;
}
header {
    margin-bottom: 15px;
    display: relative;
    padding-left: 40px;
    padding-top: 5px;
    width: 100%;
    background-color: #ff7300;
    height: 70px;
}

header .left-side {
    float: left;
    margin-top: 12px;
}

header .left-side h1 {
    display: inline-block;
    font-family: 'Merienda', cursive;
}

header .left-side h1:hover {
    cursor: default;
}

header .right-side {
    float: right;
}

header .right-side h3 {
    display: inline-block;
    margin-top: 15px;
    margin-right: 20px;
    color: #575757;
}

header .right-side button {
    margin-top: 10px;
    margin-right: 15px;
    width: 70px;
    height: 40px;
    color: #676767;
}
.eventslist {
    display: inline-block;
    float: left;
    width: 70%;
    margin-right: 15px;
}

.filterColumn {
    float: right;
    margin-right: 30px;
    padding: 15px;
    width: 24%
}

.filterColumn h1 {
    color: #fe5800;
    margin-bottom: 14px;
}

.filterColumn input {
    display: block;
    width: 100%;
    margin-bottom: 12px;
    height: 35px;
    padding: 6px;
    font-size: 17px;
}

input.inline-inputs {
    width: 30%;
    display: inline-block;
    margin-right: 13px;
}

.filterColumn button {
    width: 100%;
    height: 30px;
    margin-bottom: 50px;
}

input.last-child {
    width: 30%;
    display: inline-block;
    margin-right: 0;
}

.eventslist table {
    width: 100%;
}

.eventslist table tr {
    margin-bottom: 0;
}

.eventslist table tr td {
    text-align: center;
    border-right: 1px solid #000;
    border-left: 1px solid #000;
    border-bottom: 1px solid #000;
    margin-bottom: 0;
}
h3#filter {
    color: #fe5800;
}

#user-filter p {
    margin-bottom: 12px;
}

#user-filter select {
    display: inline;
}
